@media (min-width: 769px){
    .footer{
        width: 100%;
    }
    .city-input{
        width: 40%;
        margin: 20px;
    }
    
    .all-contents{
        background-color: black;
        height: 100vh;
    }
    .container{
        margin: 0;
        padding: 0;
        max-width: 100%;
    }
}

/* Responsive Styles */
@media (max-width: 768px) {
    .weather-temp {
        font-size: 60px; /* Further reduce for medium screens */
    }

    .weather-location {
        font-size: 30px; /* Further reduce for medium screens */
    }

    .data {
        font-size: 24px; /* Adjust for medium screens */
    }

    .text {
        font-size: 14px; /* Adjust for medium screens */
    }
    .all-contents{
        background-color: black;
        height: 100vh;
    }
    .container{
        margin: 0;
        padding: 0;
        max-width: 100%;
    }
    .top-bar{
        margin:0 20px 0 20px;
    }

}

@media (max-width: 480px) {
    .search-icon {
        display: none; /* Hide search icon on narrow screens */
    }

    .top-bar input {
        font-size: 16px; /* Adjust for small screens */
        width: 100%;
    }

    .weather-temp {
        font-size: 40px; /* Further reduce for small screens */
    }

    .weather-location {
        font-size: 24px; /* Further reduce for small screens */
    }

    .dataContainer {
        flex-direction: column; /* Stack data vertically on small screens */
        align-items: center;
        justify-content: center;
    }

    .element {
        margin-bottom: 10px; /* Add margin for stacked elements */
    }
}

.content-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


}

/* Footer Styles */
.footer {
    color: white;
